/* @jsx jsx */
import React, { useState, useEffect } from 'react';
import Head from 'next/head';
import theme from '../parts/theme';
import { jsx } from 'theme-ui';
import { ThemeProvider } from 'theme-ui';

const Home = () => {
  let [j_pressed, setJPress] = useState(false);
  let [k_pressed, setKPress] = useState(false);
  let [l_pressed, setLPress] = useState(false);
  let [semicolon_pressed, setSemicolonPress] = useState(false);
  let [x_pressed, setXPress] = useState(false);
  let [s_pressed, setSPress] = useState(false);

  function downHandler(e) {
    switch (e.key) {
      case 'j':
        setJPress(true);
        break;
      case 'k':
        setKPress(true);
        break;
      case 'l':
        setLPress(true);
        break;
      case ';':
        setSemicolonPress(true);
        break;
      case 'x':
        setXPress(true);
        break;
      case 's':
        setSPress(true);
        break;
    }
  }
  function upHandler(e) {
    switch (e.key) {
      case 'j':
        setJPress(false);
        break;
      case 'k':
        setKPress(false);
        break;
      case 'l':
        setLPress(false);
        break;
      case ';':
        setSemicolonPress(false);
        break;
      case 'x':
        setXPress(false);
        break;
      case 's':
        setSPress(false);
        break;
    }
  }

  let key_map = {
    j: { val: j_pressed, set: setJPress },
    k: { val: k_pressed, set: setKPress },
    l: { val: l_pressed, set: setLPress },
    ';': { val: semicolon_pressed, set: setSemicolonPress },
    x: { val: x_pressed, set: setXPress },
    s: { val: s_pressed, set: setSPress },
  };

  useEffect(() => {
    window.addEventListener('keydown', downHandler);
    window.addEventListener('keyup', upHandler);
    // Remove event listeners on cleanup
    return () => {
      window.removeEventListener('keydown', downHandler);
      window.removeEventListener('keyup', upHandler);
    };
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <div>
        <style jsx global>{`
          * {
            box-sizing: border-box;
          }
          html {
            font-family: ${theme.fonts.body};
            font-size: ${theme.fontSizes[0]}px;
            background: ${theme.colors.background};
            line-height: ${theme.lineHeights.body};
          }
          body {
            margin: 0;
          }
          textarea {
            font-family: inherit;
            font-size: inherit;
            line-height: inherit;
          }
        `}</style>

        <Head>
          <title>Etch</title>
        </Head>
        <div
          sx={{
            background: 'white',
            height: `calc(100vh - ${1.5 * 2}rem)`,
            width: '100%',
          }}
        />

        <div
          style={{
            display: 'flex',
            position: 'fixed',
            bottom: theme.space[1],
            left: '1ch',
            right: '1ch',
          }}
        >
          <div
            style={{
              display: 'flex',
              width: '100%',
              justifyContent: 'space-between',
            }}
          >
            <div sx={{ display: 'flex' }}>
              {'xs'.split('').map(l => (
                <div
                  sx={{
                    position: 'relative',
                    width: '2ch',
                    textAlign: 'center',
                    background: key_map[l].val ? 'pink' : 'transparent',
                    mr: 1,
                  }}
                >
                  {l}
                  <div
                    css={{
                      position: 'absolute',
                      left: -0.5,
                      top: -0.5,
                      right: -0.5,
                      bottom: -0.5,
                      border: 'solid 1px black',
                      pointerEvents: 'none',
                    }}
                  />
                </div>
              ))}
            </div>
            <div style={{ display: 'flex' }}>
              {'jkl;'.split('').map(l => (
                <div
                  sx={{
                    position: 'relative',
                    width: '2ch',
                    textAlign: 'center',
                    background: key_map[l].val ? 'pink' : 'transparent',
                  }}
                >
                  {l}
                  <div
                    css={{
                      position: 'absolute',
                      left: -0.5,
                      top: -0.5,
                      right: -0.5,
                      bottom: -0.5,
                      border: 'solid 1px black',
                      pointerEvents: 'none',
                    }}
                  />
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </ThemeProvider>
  );
};

export default Home;
